// Ëé∑ÂèñÁîªÂ∏ÉÂíå‰∏ä‰∏ãÊñá  
const canvas = document.getElementById("gameCanvas");  
const ctx = canvas.getContext("2d");  

// ËÆæÁΩÆÁîªÂ∏ÉÂ§ßÂ∞è  
canvas.width = 640;  
canvas.height = 480;  

// ÂÆö‰πâÁêÉÂíåÁ†ñÂùóÁöÑÂ±ûÊÄß  
const ballRadius = 10;  
let x = canvas.width / 2;  
let y = canvas.height - 30;  
let dx = 2;  
let dy = -2;  

const paddleHeight = 10;  
const paddleWidth = 75;  
let paddleX = (canvas.width - paddleWidth) / 2;  

// Á†ñÂùóËÆæÁΩÆ  
const brickRowCount = 5;  
const brickColumnCount = 3;  
const brickWidth = 75;  
const brickHeight = 20;  
const brickPadding = 10;  
const brickOffsetTop = 30;  
const brickOffsetLeft = 30;  

let bricks = [];  
const brickColors = ["#FF5733", "#33FF57", "#3357FF", "#F0E68C", "#FF33A6"]; // ÂÆö‰πâ‰∏çÂêåÈ¢úËâ≤ÁöÑÁ†ñÂùó  
for (let c = 0; c < brickColumnCount; c++) {  
    bricks[c] = [];  
    for (let r = 0; r < brickRowCount; r++) {  
        bricks[c][r] = { x: 0, y: 0, status: 1, color: brickColors[r % brickColors.length] }; // Ê†πÊçÆË°åÈÄâÊã©È¢úËâ≤  
    }  
}  

let score = 0;  
let isRightPressed = false;  
let isLeftPressed = false;  

// Â§ÑÁêÜÈîÆÁõò‰∫ã‰ª∂  
document.addEventListener("keydown", keyDownHandler);  
document.addEventListener("keyup", keyUpHandler);  

function keyDownHandler(e) {  
    if (e.key === "Right" || e.key === "ArrowRight") {  
        isRightPressed = true;  
    } else if (e.key === "Left" || e.key === "ArrowLeft") {  
        isLeftPressed = true;  
    }  
}  

function keyUpHandler(e) {  
    if (e.key === "Right" || e.key === "ArrowRight") {  
        isRightPressed = false;  
    } else if (e.key === "Left" || e.key === "ArrowLeft") {  
        isLeftPressed = false;  
    }  
}  

// Á¢∞ÊíûÊ£ÄÊµã  
function collisionDetection() {  
    for (let c = 0; c < brickColumnCount; c++) {  
        for (let r = 0; r < brickRowCount; r++) {  
            const b = bricks[c][r];  
            if (b.status === 1) {  
                if (x > b.x && x < b.x + brickWidth && y > b.y && y < b.y + brickHeight) {  
                    dy = -dy;  
                    b.status = 0;  
                    score++;  
                    if (score === brickRowCount * brickColumnCount) {  
                        alert("ÊÅ≠Âñú‰Ω†ÔºåËé∑ËÉú‰∫ÜÔºÅ");  
                        document.location.reload();  
                    }  
                }  
            }  
        }  
    }  
}  

// ÁªòÂà∂ÁêÉ„ÄÅÊå°ÊùøÂíåÁ†ñÂùó  
function drawBall() {  
    ctx.beginPath();  
    ctx.arc(x, y, ballRadius, 0, Math.PI * 2);  
    ctx.fillStyle = "#0095DD";  
    ctx.fill();  
    ctx.closePath();  
}  

function drawPaddle() {  
    ctx.beginPath();  
    ctx.rect(paddleX, canvas.height - paddleHeight, paddleWidth, paddleHeight);  
    ctx.fillStyle = "#0095DD";  
    ctx.fill();  
    ctx.closePath();  
}  

function drawBricks() {  
    for (let c = 0; c < brickColumnCount; c++) {  
        for (let r = 0; r < brickRowCount; r++) {  
            const b = bricks[c][r];  
            if (b.status === 1) {  
                const brickX = c * (brickWidth + brickPadding) + brickOffsetLeft;  
                const brickY = r * (brickHeight + brickPadding) + brickOffsetTop;  
                b.x = brickX;  
                b.y = brickY;  
                ctx.beginPath();  
                ctx.rect(brickX, brickY, brickWidth, brickHeight);  
                ctx.fillStyle = b.color; // ‰ΩøÁî®Á†ñÂùóÁöÑÈ¢úËâ≤  
                ctx.fill();  
                ctx.closePath();  
            }  
        }  
    }  
}  

function drawScore() {  
    ctx.font = "16px Arial";  
    ctx.fillStyle = "#0095DD";  
    ctx.fillText("ÂàÜÊï∞: " + score, 8, 20);  
}  

// ‰∏ªË¶ÅÁªòÂà∂ÂáΩÊï∞  
function draw() {  
    ctx.clearRect(0, 0, canvas.width, canvas.height);  
    drawBricks();  
    drawBall();  
    drawPaddle();  
    drawScore();  
    collisionDetection();  

    // Â∞èÁêÉÁßªÂä®  
    x += dx;  
    y += dy;  

    // Á¢∞ÊíûÊ£ÄÊµãËæπÁïå  
    if (x + dx > canvas.width - ballRadius || x + dx < ballRadius) {  
        dx = -dx;  
    }  
    if (y + dy < ballRadius) {  
        dy = -dy;  
    } else if (y + dy > canvas.height - ballRadius) {  
        if (x > paddleX && x < paddleX + paddleWidth) {  
            dy = -dy;  
        } else {  
            alert("Ê∏∏ÊàèÁªìÊùüÔºÅ");  
            document.location.reload();  
        }  
    }  

    // ÊéßÂà∂Êå°ÊùøÁßªÂä®  
    if (isRightPressed && paddleX < canvas.width - paddleWidth) {  
        paddleX += 7;  
    } else if (isLeftPressed && paddleX > 0) {  
        paddleX -= 7;  
    }  

    requestAnimationFrame(draw);  
}  

// ÂêØÂä®Ê∏∏Êàè  
draw();